import React, { Component } from 'react';

// import * as firebase from 'firebase';

class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        return ( 
                <ul className='item-container'>
                        <li className='item item-menu'>
                            <button 
                                className='item-button' 
                                type='button' 
                                name='enCourse'
                                onClick={this.props.choixComponent}
                            >En course!
                        </button>
                        </li>
                        <li className='item item-menu'>
                            <button 
                              className='item-button' 
                              type='button' 
                              name='mesListes'
                              onClick={this.props.choixComponent}
                            >Mes listes
                            </button>
                        </li>
                        <li className='item item-menu'>
                            <button 
                              className='item-button' 
                              type='button' 
                              name='monProfile'
                              onClick={this.props.choixComponent}
                            >Mon profile
                            </button>
                        </li>
                    </ul>
              
         );
    }
}
 
export default Menu;